import bus from "../utils/bus.js"
import titleComponent from './titleComponent.vue';
import listItem from './listItemComponent.vue';
import uniLoadMore from '../components/uni-load-more.vue';
import {
	getMyStoryList,
	deleteMyStory
} from "../../api/people/api.js"
export default {
	components: {
		listItem: listItem,
		titleComponent: titleComponent,
		uniLoadMore: uniLoadMore
	},
	data() {
		return {
			selectID: '',
			page_id: 1,
			modalShow: false,
			isShownBack: true,
			userInfo: {
				userName: '',
				avatar: '',
			},
			currLength: 0,
			loadingText: '加载中...',
			loadingType: 0, //定义加载方式 0---contentdown  1---contentrefresh 2---contentnomore
			contentText: {
				contentdown: '上拉显示更多',
				contentrefresh: '正在加载...',
				contentnomore: '没有更多了哦'
			},
			title: '我的故事',
			// list的数据
			// type: 0->文字   1->语音    2->视频
			list: [],
		}
	},
	onReachBottom: function() {
		//触底的时候请求数据，即为上拉加载更多
		//为了更加清楚的看到效果，添加了定时器
		this.getmorenews();
	},
	methods: {
		onStart(){
			const _self = this
			this.page_id = 1;
			getMyStoryList(this.page_id).then((res) => {
				if (res.data) { //_self.list = _self.list.concat(res.data.storyList)
					console.log(res.data, "data")
					const temp = JSON.parse(JSON.stringify(res.data.storyList));
					_self.list = [];
					console.log(temp, "temp")
					for (var i = 0; i < temp.length; i++) {
						_self.list.push({
							sid: temp[i].sid,
							name: temp[i].username,
							time: temp[i].pubDate, //无
							place: temp[i].location_name,
							avatar: temp[i].avatar,
							desc: temp[i].detail
						})
					}
					_self.page_id++
					_self.currLength = _self.list.length
				} else {
					uni.showToast({
						icon: 'loading',
						title: '获取数据失败'
					})
				}
			}).catch((err) => {
				console.log(err)
			})
		},
		getAvatar() {
			//console.log(this.userInfo)
			return this.userInfo.avatar
		},
		getmorenews: function() {
			if (this.loadingType !== 0) { //loadingType!=0;直接返回
				return false;
			}
			this.loadingType = 1;
			uni.showNavigationBarLoading(); //显示加载动画
			const _self = this
			getMyStoryList(this.page_id).then((res) => {
				/*if (res.data) {
					if (res.data.storyList && res.data.storyList.length === 0) {
						_self.loadingType = 2
					} else {
						_self.list = _self.list.concat(res.data.storyList)
						_self.page_id++
						this.loadingType = 0;
					}
				}*/
				if (res.data.storyList.length != 0) {
					console.log(res.data, "data")
					const temp = JSON.parse(JSON.stringify(res.data.storyList));
					//_self.list = [];
					for (var i = 0; i < temp.length; i++) {
						// const name_may_miss = "匿名用户";
						// const avatar_may_miss = "../../static/avatar_miss.jpg";
						// if (temp[i].isShow) {
						// 	name_may_miss = temp[i].username;
						// 	avatar_may_miss = temp[i].avatar;
						// }
						_self.list.push({
							sid: temp[i].sid,
							name: temp[i].username,
							time: temp[i].pubDate,
							place: temp[i].location_name,
							avatar: temp[i].avatar,
							desc: temp[i].detail
						})
					}
					//_self.list = _self.list.concat(res.data.storyList)
					_self.page_id++
					//console.log("afterGetMore", this.page_id);
					_self.currLength = _self.list.length
					_self.loadingType = 0;
				} else if (res.data.code == 1001) {
					_self.loadingType = 2;
					_self.page_id--;
					console.log("botton page", this.page_id);
					uni.showToast({
						icon: 'none',
						title: '没有更多了哦！'
					})
				} else {
					uni.showToast({
						icon: 'loading',
						title: '获取数据失败'
					})
				}
			}).catch((err) => {
				console.log(err)
			})
		},
		// 这里写退出
		back() {
			uni.switchTab({
				url: '/pages/people/people_main',
			});
		},
		// 删除某一项
		deleteItem(itemID) {
			// 这里发起一个删除的信息
			// 弹出弹窗询问是否删除
			this.modalShow = true;
			this.selectID = itemID;
			
		},
		// 跳转查看详情
		showDetail(itemID) {
			uni.navigateTo({
				url: `../story/storyDetails?sid=${itemID}`, 
			});
		},
		confirm() {
			// 这里要写异步删除
			//console.log("删除", this.selectID)
			const _self = this
			deleteMyStory(this.selectID).then((res) => {
				console.log(res);
				if (res.data && res.data.code==1001) {
					uni.showToast({
						title: '删除成功'
					})
					_self.modalShow = false;
					_self.onStart();
				} else {
					uni.showToast({
						icon: 'loading',
						title: '请稍后再试'
					})
					_self.modalShow = false;
				}
			}).catch((err) => {
				uni.showToast({
					icon: 'loading',
					title: '请稍后再试'
				})
				_self.modalShow = false;
			})
		}
	},
	created() {
		bus.$on("hello", msg => {
			console.log(msg, "lll")
		})
	},
	onLoad(data) {
		this.userInfo.name = this.$store.state.userInfo.userName;
		this.userInfo.avatar = this.$store.state.userInfo.avatar;
	},
	onShow() {
		this.onStart();
	},
}
